import { types, Instance, SnapshotIn, SnapshotOut } from 'mobx-state-tree';

import ProductItem, { ProductItemModel } from './product';

const Products = types
    .model('Products', {
        title: types.optional(types.string, ''),
        items: types.array(ProductItem),
        selectedId: types.maybeNull(types.string),
    })
    .actions((self) => {
        const products = self;
        const setVisibility = (item: ProductItemModel, value: boolean, index: number | null): void => {
            item.visible = value;
            item.index = index;
        };

        const selectItem = (itemId: string): void => {
            if (products.items.find((item) => item.id === itemId)) {
                products.selectedId = itemId;
            }
        };

        return {
            setVisibility,
            selectItem,
        };
    });

export default Products;

export type ProductsModel = Instance<typeof Products>;
export type ProductsSnapshotOut = SnapshotOut<typeof Products>;
export type ProductsSnapshotIn = SnapshotIn<typeof Products>;
