import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { environment } from 'configs';
import { Catalog } from 'pages/Catalog/Catalog';
import { Cart } from 'pages/Cart/Cart';
import { Status } from 'pages/Status/Status';
import { Promo } from 'pages/Promo/Promo';
import { NoArea } from 'pages/NoArea/NoArea';
import { AddressSimple } from 'pages/AddressSimple/AddressSimple';
import { routes } from 'stores/ui';
import 'style/global.scss';
import 'normalize.css/normalize.css';

import './style.scss';

const App: React.FC = () => {
    const fallback = <div>Loading</div>;

    const containerStyle =
        environment === 'staging'
            ? {
                  transform: 'scale(0.4)',
                  height: '1080px',
                  width: '1920px',
              }
            : {};

    const wrapperStyle =
        environment === 'staging'
            ? {
                  transform: 'translate(-580px, -310px)',
              }
            : {};

    return (
        <div style={wrapperStyle}>
            <div style={containerStyle} className="AppContainer">
                <React.Suspense fallback={fallback}>
                    <Switch>
                        <Route path={routes.CATALOG}>
                            <Catalog />
                        </Route>
                        <Route path={routes.CART}>
                            <Cart />
                        </Route>
                        <Route path={routes.STATUS}>
                            <Status />
                        </Route>
                        <Route path={routes.ADDRESS_SIMPLE}>
                            <AddressSimple />
                        </Route>
                        <Route path={routes.PROMO}>
                            <Promo />
                        </Route>
                        <Route path={routes.NO_AREA}>
                            <NoArea />
                        </Route>
                        <Redirect to={routes.CATALOG} />
                    </Switch>
                </React.Suspense>
            </div>
        </div>
    );
};

export default App;
