import React from 'react';

import './Suggest.scss';

interface SuggestProps {
    items: string[];
}

export const Suggest: React.FC<SuggestProps> = ({ items }) => (
    <div className="Suggest">
        {items.map((item) => (
            <div className="Suggest-Item" key={item}>
                {item}
            </div>
        ))}
    </div>
);
