import { types, Instance, SnapshotIn, SnapshotOut } from 'mobx-state-tree';
import history from 'tools/history';

export const SEARCH = 'SEARCH';
export const CATALOG = 'CATALOG';
export const CART = 'CART';
export const ADDRESS_SIMPLE = 'ADDRESS_SIMPLE';
export const STATUS = 'STATUS';
export const PROMO = 'PROMO';
export const NO_AREA = 'NO_AREA';

export const routes = {
    [SEARCH]: '/search',
    [CATALOG]: '/catalog',
    [CART]: '/cart',
    [ADDRESS_SIMPLE]: '/address-simple',
    [STATUS]: '/status',
    [PROMO]: '/promo',
    [NO_AREA]: '/no-area',
};

const Ui = types
    .model('Ui', {
        page: types.optional(types.enumeration(Object.values(routes)), routes.PROMO),
        previous: types.maybeNull(types.enumeration(Object.values(routes))),
        visibles: types.array(types.string),
    })
    .actions((self) => {
        const ui = self;

        const gotoPage = (to: string) => {
            ui.previous = ui.page;
            ui.page = to;
            history.push(to);
        };

        const setVisible = (itemId: string): void => {
            ui.visibles.push(itemId);
        };

        const removeVisible = (itemId: string): void => {
            ui.visibles.splice(ui.visibles.indexOf(itemId), 1);
        };

        const getIndex = (itemId: string): number => ui.visibles.indexOf(itemId) + 1;

        const goToCart = () => {
            gotoPage(routes.CART);
            window.AssistantClient.sendData({
                // eslint-disable-next-line @typescript-eslint/camelcase
                action_id: 'GO_TO_CART',
            });
        };

        return {
            gotoPage,
            goToCart,
            setVisible,
            removeVisible,
            getIndex,
        };
    });

export type UiModel = Instance<typeof Ui>;
export type UiSnapshotOut = SnapshotOut<typeof Ui>;
export type UiSnapshotIn = SnapshotIn<typeof Ui>;

export default Ui;
