import 'core-js';
import React from 'react';
import ReactDOM from 'react-dom';
import { Router } from 'react-router-dom';
import history from 'tools/history';
import store from 'stores';
import { environment } from 'configs';
import { setStore } from 'tools/connect';

import browserContols from './tools/browsers-control/index';
import AppContainer from './containers/AppContainer';

import 'tools/new-spatial-navigation';

setStore(store);

if (environment !== 'production') {
    window.store = store;
    const makeInspectable = require('mobx-devtools-mst'); // eslint-disable-line
    makeInspectable.default(store);
}

export const browserVerify = browserContols.browserVerify();

const APP_ROOT_ID = 'application_root';

export function renderApp(App: React.FC<{}>): void {
    ReactDOM.render(
        <Router history={history}>
            <App />
        </Router>,
        document.getElementById(APP_ROOT_ID),
    );
}

if (browserVerify.isNotAllowedBrowser) {
    document.body.appendChild(browserVerify.nodeElement);
} else {
    renderApp(AppContainer);
}

if (environment !== 'production' && module.hot) {
    module.hot.accept('./containers/AppContainer', () => {
        // eslint-disable-next-line
        const NextApp = require('./containers/AppContainer').default;
        renderApp(NextApp);
    });
}
