import React from 'react';
import { applySnapshot } from 'mobx-state-tree';
import { isDevelopment } from 'configs';
import { USER_MOCK } from 'mocks';
import { Suggest } from 'components/Suggest/Suggest';
import { Header } from 'components/Header/Header';
import { connectToStore } from 'tools/connect';
import { StoreType } from 'stores/types';
import withBem from 'hoc/withBem';

import { CartInfo } from './Info/CartInfo';
import { CartList } from './List/CartList';
import './Cart.scss';

interface CartProps extends StoreType, BemProps {}

const Component: React.FC<CartProps> = ({ user, bem }) => {
    React.useEffect(() => {
        const onKeyDown = (event: any) => {
            switch (event.code) {
                case 'Digit1':
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'REMOVE_ITEM',
                            item: '32e5b884-fa98-11e8-80c5-0cc47a817925',
                            count: 2,
                        },
                    });
                    break;
                case 'Digit2':
                    applySnapshot(user, USER_MOCK);
                    break;
                default:
                    break;
            }
        };

        document.addEventListener('keydown', onKeyDown);

        return () => {
            document.removeEventListener('keydown', onKeyDown);
        };
    }, [user]);

    return (
        <>
            <div className={bem('background')} />
            <Header title="Корзина" />
            <div className={bem('container')}>
                <CartList />
                <CartInfo />
            </div>
            {isDevelopment ? <Suggest items={['1. Удали два средних черничных попкорна', '2. Дополнить адрес']} /> : ''}
        </>
    );
};

export const Cart = withBem(connectToStore(Component), 'Cart');
