import React from 'react';
import { SHOW_ITEMS_MOCK } from 'mocks';
import { isDevelopment } from 'configs';
import { Suggest } from 'components/Suggest/Suggest';
import { Header } from 'components/Header/Header';
import { HeaderAddress } from 'components/HeaderAddress/HeaderAddress';
import { HeaderCart } from 'components/HeaderCart/HeaderCart';
import { connectToStore } from 'tools/connect';
import { StoreType } from 'stores/types';

import { CatalogList } from './List/CatalogList';
import { CatalogSkeleton } from './Skeleton/CatalogSkeleton';

type CatalogProps = StoreType;

const Component: React.FC<CatalogProps> = ({ cart, products }) => {
    React.useEffect(() => {
        console.log('ask for stock');
        window.AssistantClient.sendData({
            // eslint-disable-next-line @typescript-eslint/camelcase
            action_id: 'ASK_FOR_STOCK',
        });
    }, []);

    React.useEffect(() => {
        if (isDevelopment) {
            window.AssistantClient.onData({
                type: 'smart_app_data',
                // eslint-disable-next-line
                smart_app_data: {
                    command: 'SHOW_ITEMS',
                    stock: SHOW_ITEMS_MOCK,
                },
            });

            window.AssistantClient.onData({
                type: 'smart_app_data',
                // eslint-disable-next-line
                smart_app_data: {
                    command: 'ADD_ITEM',
                    item: '21ff407a-fa98-11e8-80c5-0cc47a817925',
                    count: '2',
                },
            });
        }
    }, []);

    React.useEffect(() => {
        const onKeyDown = (event: any) => {
            switch (event.code) {
                case 'Digit1':
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'SHOW_ITEMS',
                            stock: SHOW_ITEMS_MOCK,
                        },
                    });
                    break;
                case 'Digit2':
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'REMOVE_ITEM',
                            item: '32e5b884-fa98-11e8-80c5-0cc47a817925',
                            count: '2',
                        },
                    });
                    break;
                case 'Digit3':
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'REMOVE_ALL',
                        },
                    });
                    break;
                default:
            }
        };

        document.addEventListener('keydown', onKeyDown);

        return () => {
            document.removeEventListener('keydown', onKeyDown);
        };
    }, []);

    return (
        <>
            <Header title="Самокат">
                <HeaderAddress />
                {cart.count > 0 ? <HeaderCart /> : ''}
            </Header>
            {products.items.length ? <CatalogList /> : <CatalogSkeleton />}
            {isDevelopment ? (
                <Suggest items={['1. Показать каталог', '2. Удали два средних черничных попкорна', '3. Удали все']} />
            ) : (
                ''
            )}
        </>
    );
};

export const Catalog = connectToStore(Component);
